<template>
    <div class="application">
      <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          prop="original_wechat"
          label="微信号"
          width="180">
        </el-table-column>
        <el-table-column
          prop="phone_number"
          label="手机号"
          width="180">
        </el-table-column>
         <el-table-column
          prop="name"
          label="名字"
          >
        </el-table-column>
        <el-table-column
          prop="current_fans"
          label="当前粉丝量">
        </el-table-column>
        <el-table-column
          label="代理地区">
           <template slot-scope="city">
           <div>
            {{city.row.province}}
            {{city.row.city}}
            {{city.row.county}}
          </div>
          </template>
        </el-table-column>
         <el-table-column
          label="微信收款二维码"
          width="300"
          >
          <template slot-scope="imgscope">
          <div style="width:100px;height:100px; margin:0 auto;" @click="handlecollection(imgscope.row.collection_QR_code)">
            <img :src="imgscope.row.collection_QR_code" alt="" width="100" height="100">
          </div>
          </template>
        </el-table-column>
          <el-table-column
          label="微信二维码"
          width="300"
         >
          <template slot-scope="imgscope">
          <div style="width:100px;height:100px; margin:0 auto;" @click="handlewechat(imgscope.row.wechat_QR_code)">
            <img :src="imgscope.row.wechat_QR_code" alt="" width="100" height="100">
          </div>
          </template>
        </el-table-column>
         <el-table-column
          prop="create_time"
          label="申请时间">
        </el-table-column>
          <el-table-column
          label="操作"
          width="260"
          >
          <template slot-scope="scope">
              <el-button type="success" @click="handleEgis(scope.row)">审核通过</el-button>
              <el-button type="warning" @click="handleNoEgis(scope.row)">审核不通过</el-button>
          </template>
        </el-table-column>
      </el-table>

         <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="5"
          layout="total, prev, pager, next, jumper"
          :total="totalpage">
        </el-pagination>
        <el-dialog
         title="原图片"
         :visible.sync="dialogVisible"
         width="30%">
          <img :src="imgData" alt="原图片大小" style="margin-left:150px;">
           <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
          </span>
        </el-dialog>
    </div>
</template>

<script>
import {AgentReview, GetagentReview} from '@/api/index'
export default {
  name: 'Applicationaudit',
  data () {
    return {
      currentPage: 1,
      totalpage: 1,
      tableData: [],
      imgData: '',
      dialogVisible: false
    }
  },
  methods: {
    // 获取代理待审核列表
    getagenreview (page) {
      GetagentReview(page)
        .then(res => {
          if (res.errno === '0') {
            this.tableData = res.agent_info
            this.totalpage = res.count
            this.currentPage = res.page
          } else if (res.errno === '4004') {
            alert('请重新登录')
            localStorage.setItem('username', '')
            localStorage.setItem('mytoken', '')
            this.$router.push({name: 'login'})
          }
        })
    },
    // 审核通过
    handleEgis (row) {
      let id = row.agent_id
      let params = {status: 1, agent_id: id}
      AgentReview(JSON.stringify(params))
        .then(res => {
          if (res.errno === '0') {
            this.$message({
              message: '审核操作成功',
              type: 'success'
            })
            this.getagenreview()
          } else {
            alert(res.errmsg)
          }
        })
    },
    // 审核不通过
    handleNoEgis (row) {
      let id = row.agent_id
      let params = {status: 2, agent_id: id}
      AgentReview(JSON.stringify(params))
        .then(res => {
          if (res.errno === '0') {
            alert('操作成功')
            this.getagenreview()
          } else {
            alert(res.errmsg)
          }
        })
    },
    // 每页显示的条数改变
    handleSizeChange (val) {
    },
    // 当前页数的改变
    handleCurrentChange (val) {
      this.currentPage = val
      this.getagenreview(val)
    },
    handlewechat (row) {
      this.dialogVisible = true
      this.imgData = row
    },
    handlecollection (row) {
      this.dialogVisible = true
      this.imgData = row
    }
  },
  mounted () {
    this.getagenreview()
  }
}
</script>

<style lang="scss" scoped>

</style>



