<template>
  <div class="statistics">
     <div id="statisticsEcharts" style="height:500px;width:500px;float:left;">
     </div>
       <div id="lineEcharts" style="height:500px;width:500px;float:left;">
       </div>
  </div>
</template>

<style lang="scss" scoped>
.statistics{
  overflow: hidden;
}
</style>

<script>
export default {
  props: ['weekData'],
  mounted () {
    for (var i=0; i < this.weekData.stat_info.length; i++) {
      this.xAxisdata.push(this.weekData.stat_info[i].create_time.substr(5, 5))
      this.yAxisdata.push(this.weekData.stat_info[i].today_fans)
      this.ytotal.push(this.weekData.stat_info[i].total_fans)
    }
    this.drawBar()
    this.drawLine()
  },
  data () {
    return {
      xAxisdata: [],
      yAxisdata: [],
      ytotal: []
    }
  },
  methods: {
    drawBar () {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById('statisticsEcharts'))
      // 绘制图表
      myChart.setOption({
        title: { text: '每日粉丝增加量统计' },
        tooltip: {},
        xAxis: {
          data: this.xAxisdata
        },
        yAxis: {},
        series: [{
          name: '每天粉丝增加量',
          type: 'bar',
          data: this.yAxisdata
        }]
      })
    },
    drawLine () {
      let myChart = this.$echarts.init(document.getElementById('lineEcharts'))
      myChart.setOption({
        title: { text: '7天粉丝总量统计' },
        tooltip: {},
        xAxis: {
          data: this.xAxisdata
        },
        yAxis: {},
        series: [{
          name: '每天粉丝总量',
          type: 'line',
          data: this.ytotal
        }]
      })
    }
  }
}
</script>



