 <template>
 <div class="home">
    <el-container>
      <!-- 侧边栏部分 -->
      <el-aside width="auto">
        <div class="logo"></div>
        <el-menu
          class="el-menu-admin"
          :collapse="isCollapse"
          background-color="#f9f9f9"
          :router="true"
          :default-active="activeNav"
          >
          <el-submenu index="1">
            <template slot="title">
                <i class="el-icon-menu"></i>
                <router-link tag="span" to="wecome">首页</router-link>
            </template>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span>代理申请管理</span>
            </template>

            <el-menu-item index="applicationaudit">
              <i class="el-icon-menu"></i>
              <span>代理申请审核</span>
            </el-menu-item>

             <el-menu-item index="applicationlist">
              <i class="el-icon-menu"></i>
              <span>代理列表</span>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-container>
        <!-- 这是头部 -->
        <el-header>
          <i class="myicon myicon-menu toggle-btn" @click="toggleMenu"></i>
          <div class="system-title">
           后台管理系统
          </div>
          <div class="welcome">
            <span>
              您好，{{username}}
            </span>
            <el-button type="warning" round @click="logout">退出</el-button>
          </div>
        </el-header>

        <!-- 中间主体内容部分， -->
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import {getActiveNav} from '@/js/getActiveNav'
export default {
  name: 'Home',
  data () {
    return {
      isCollapse: false,
      username: 'admin',
      activeNav: '1'
    }
  },
  methods: {
    toggleMenu () {
      this.isCollapse = !this.isCollapse
    },
    logout () {
      // 退出时将清除掉token，并且跳转回到login页面
      this.$router.push({name: 'login'})
      localStorage.setItem('username', '')
      localStorage.setItem('mytoken', '')
    }
  },
  created () {
    this.username = localStorage.getItem('username')
    // 实现页面刷新时左边侧栏高亮
    this.activeNav = getActiveNav(window.location.href)
  }
}
</script>

<style lang="scss" scoped>
.home {
  height: 100%;
  // 这里表示不是折叠的时候的menu的样式
  .el-menu-admin:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
  .el-container {
    height: 100%;
  }
  .el-aside {
    background-color: #F9F9F9;
  }
  .el-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f5801f;
  }
  .logo {
    height:70px;
    background: url(../assets/logo.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-color: white;
    box-sizing: border-box;
  }
  .toggle-btn {
    padding: 0 15px;
    margin-left: -20px;
    font-size: 36px;
    color: white;
    cursor: pointer;
    line-height: 60px;
    &:hover {
      background-color: #f5801f;
    }
  }
  .system-title {
    font-size: 28px;
    color: white;
  }
  .welcome, {
    color: white;
  }
}
</style>



