import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/login'
import Home from '@/views/home'
import Wecome from '@/components/wecome'
import Applicationaudit from '@/components/application/Applicationaudit'
import Applicationlist from '@/components/application/Applicationlist'
import FinancialManagement from '@/components/financial/FinancialManagement'

Vue.use(Router)

export default new Router({
  routes: [
    {
      name: 'login',
      path: '/login',
      component: Login
    },
    {
      name: 'home',
      path: '/home',
      component: Home,
      redirect: {name: 'wecome'}, // 重定向到wecome路由
      children: [{
        name: 'wecome',
        path: 'wecome',
        component: Wecome
      },
      {name: 'applicationaudit', path: 'applicationaudit', component: Applicationaudit},
      {name: 'applicationlist', path: 'applicationlist', component: Applicationlist},
      {name: 'financialManagement', path: 'financialManagement', component: FinancialManagement}
      ]
    },
    {
      path: '*',
      redirect: {name: 'wecome'}
    }
  ]
})



