import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  username: ''
}
const getters = {
  username (state) {
    // 返回一个本地存储的用户名
    return state.username === '' ? localStorage.getItem('myname') : state.username
  }
}
const mutations = {
  getusername (state, name) {
    // 获取登录的用户名称
    state.username = name
    // 将用户名存储在本地localStorage
    localStorage.setItem('myname', name)
  }
}
const actions = {
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})



