<template>
  <div class="management">
     <div id="managementEcharts" style="height:500px;width:500px">
     </div>
  </div>
</template>

<style lang="scss" scoped>

</style>

<script>
export default {
  mounted () {
    this.drawLine()
  },
  methods: {
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById('managementEcharts'))
      // 绘制图表
      myChart.setOption({
        title: { text: '财务报表' },
        tooltip: {},
        xAxis: {
          data: ['雪纺衫', '裤子', '高跟鞋', '袜子']
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'bar',
          data: [5, 20, 36, 10]
        }]
      })
    }
  }
}
</script>



