<template>
  <div class="statistics">
    <div id="statisticsEcharts" style="height:500px;width:100%;">
    </div>
    <div id="lineEcharts" style="height:500px;width:100%;">
    </div>
  </div>
</template>

<script>
export default {
  props: ['monthsData'],
  mounted () {
    console.log(this.monthsData)
    if (this.monthsData >= 10) {
      this.months = this.monthsData.month
    } else {
      this.months = this.monthsData.month.substr(1, 1)
    }
    for (var i=0; i < this.monthsData.stat_info.length; i++) {
      this.xAxisdata.push(this.monthsData.stat_info[i].create_time.substr(8, 2))
      this.yAxisdata.push(this.monthsData.stat_info[i].today_fans)
      this.ytotal.push(this.monthsData.stat_info[i].total_fans)
    }
    this.drawBar()
    this.drawLine()
  },
  data () {
    return {
      xAxisdata: [],
      yAxisdata: [],
      ytotal: [],
      months: ''
    }
  },
  methods: {
    drawBar () {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById('statisticsEcharts'))
      // 绘制图表
      myChart.setOption({
        title: { text: this.months + '月份粉丝增加量统计' },
        tooltip: {},
        xAxis: {
          data: this.xAxisdata
        },
        yAxis: {},
        series: [{
          name: '每天粉丝增加量',
          type: 'bar',
          data: this.yAxisdata
        }]
      })
    },
    drawLine () {
      let myChart = this.$echarts.init(document.getElementById('lineEcharts'))
      myChart.setOption({
        title: { text: this.months + '月份粉丝总量统计' },
        tooltip: {},
        xAxis: {
          data: this.xAxisdata
        },
        yAxis: {},
        series: [{
          name: '每天粉丝总量',
          type: 'line',
          data: this.ytotal
        }]
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.statistics{
  overflow: hidden;
}
</style>



