<template>
    <div class="login">
      <el-form ref="formLogin" :model="form" class="login-from" :rules="rules">
        <el-form-item>
            <div class="login-img">
                <img src="../assets/avatar.png" alt="登录图片" width="100">
            </div>
        </el-form-item>
        <el-form-item prop="username" label="用户名">
           <el-input v-model="form.username" prefix-icon="myicon myicon-user" placeholder="请输入用户名"></el-input>
        </el-form-item>
         <el-form-item prop="password" label="密码">
           <!-- @keydown.native.enter按下确定键的时候触发 -->
           <el-input v-model="form.password" type="password" prefix-icon="myicon myicon-key" @keydown.native.enter="handleLogin('formLogin')" placeholder="请输入密码"></el-input>
        </el-form-item>
         <el-form-item>
          <el-button class="login-btn" @click="handleLogin('formLogin')">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>

<script>
import {Login} from '@/api/index'
export default {
  name: 'Login',
  data () {
    return {
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods: {
    handleLogin (formLogin) {
      // 检验用户是否输入
      this.$refs.formLogin.validate((isValide) => {
        // 如果检验通过则发起请求
        if (isValide) {
          Login(JSON.stringify(this.form))
            .then(res => {
              if (res.errno === '0') {
                // 登录成功跳转到啊首页
                // 还要保存token到localStorage
                localStorage.setItem('username', res.data[0].username)
                localStorage.setItem('mytoken', res.data[0].token)
                this.$router.push({name: 'home'})
                // 设置用户名称
              } else {
                this.$message.error(res.errmsg)
              }
            })
        } else {
          alert('请输入用户名或者密码')
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.login{
    width: 100%;
    height: 100%;
    background-color: #2f4050;
    position: fixed;
    .login-from{
        width: 400px;
        margin: 100px auto;
        padding: 0px 40px 15px 40px;
        background-color: #fff;
        position: absolute;
        right: 0;
        left:0;
        .login-img{
             position: relative;
             left: 50%;
             width: 120px;
             height: 120px;
             margin-left: -60px;
             margin-top: -60px;
             box-sizing: border-box;
             border-radius: 50%;
             border: 10px solid #fff;
             box-shadow: 0 1px 5px #ccc;
             overflow: hidden;
        }
        .login-btn{
            width: 100%;
            background-color: blue;
            color: #fff;
            font-size: 20px;
        }
    }
}
</style>



