<template>
    <div class="applicationList">
      <!-- 表格渲染 -->
        <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          prop="original_wechat"
          label="微信号"
        >
        </el-table-column>
        <el-table-column
          prop="phone_number"
          label="手机号"
        >
        </el-table-column>
         <el-table-column
          prop="name"
          label="名字"
          >
        </el-table-column>
        <el-table-column
          prop="original_fans"
          label="当前粉丝量">
        </el-table-column>
         <el-table-column
          label="微信收款码">
          <template slot-scope="imgscope">
            <img :src="imgscope.row.collection_QR_code" alt="" width="100" height="100" @click="handlecollection(imgscope.row.collection_QR_code)">
          </template>
        </el-table-column>
         <el-table-column
          label="微信二维码">
          <template slot-scope="imgscope">
            <img :src="imgscope.row.wechat_QR_code" alt="" width="100" height="100" @click="handlewechat(imgscope.row.wechat_QR_code)">
          </template>
        </el-table-column>
        <el-table-column
          label="代理地区">
           <template slot-scope="city">
           <div>
            {{city.row.province}}
            {{city.row.city}}
            {{city.row.county}}
          </div>
          </template>
        </el-table-column>
         <el-table-column
          prop="create_time"
          label="申请时间">
        </el-table-column>
          <el-table-column
          label="操作"
          width="660"
          >
          <template slot-scope="scope">
              <el-button type="danger" @click="handleDelete(scope.row)">删除代理</el-button>
              <el-button type="primary" @click="handleAddfans(scope.row)">日常加粉</el-button>
              <el-button type="primary" @click="handleUpdatefans(scope.row)">日加粉修改</el-button>
              <el-button type="primary" @click="handleSelectweekFans(scope.row)">7天加粉统计</el-button>
              <el-button type="primary" @click="handleSelectmonthFans(scope.row)">月加粉统计</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页显示 -->
         <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="5"
          layout="total, prev, pager, next, jumper"
          :total="totalpage">
        </el-pagination>
         <!-- 日常加粉弹框 -->
         <el-dialog
            title="今日增加粉丝量"
            :visible.sync="DialogVisible"
            width="30%"
            center>
              <el-form :model="formLabelAlign">
                <el-form-item label="今日增加粉丝量">
                  <el-input v-model="formLabelAlign.number"></el-input>
                </el-form-item>
              </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="DialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="commitFans">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 粉丝量修改弹框 -->
          <el-dialog
            title="今日增加粉丝量修改"
            :visible.sync="UpdateDialogVisible"
            width="30%"
            center>
              <el-form :model="formLabelAlign">
                <el-form-item label="今日增加粉丝量修改">
                  <el-input v-model="formLabelAlign.number"></el-input>
                </el-form-item>
              </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="UpdateDialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="updateFans">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 7天粉丝量统计 -->
         <el-dialog
            title="7天粉丝量增加量统计"
            :visible.sync="weeksDialogVisible"
            width="60%"
          >
          <VermicelliStatistics :weekData="weekData" ref="vermicellistatistics" v-if="weeksDialogVisible"></VermicelliStatistics>
          <span slot="footer" class="dialog-footer">
            <el-button @click="weeksDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="weeksDialogVisible = false">确 定</el-button>
          </span>
        </el-dialog>
        <!-- 日期查詢 -->
        <el-dialog
            title="请选择需要查询的月份"
            :visible.sync="monthsDialogVisible"
            width="30%"
          >
            <el-date-picker
            v-model="value"
            type="month"
            format="yyyy 年 MM 月"
            value-format="yyyy-MM"
            placeholder="请选择需要查询的月份">
          </el-date-picker>
          <span slot="footer" class="dialog-footer">
            <el-button @click="monthsDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="monthsCommit">确 定</el-button>
          </span>
        </el-dialog>
        <!-- 月粉丝量统计 -->
        <el-dialog
            title="月粉丝量增加量统计"
            :visible.sync="monthvermicelliDialogVisible"
            width="60%"
          >
          <MonthVermicelli :monthsData="monthsData" ref="monthvermicelli" v-if="monthvermicelliDialogVisible"></MonthVermicelli>
          <span slot="footer" class="dialog-footer">
            <el-button @click="monthvermicelliDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="monthvermicelliDialogVisible = false">确 定</el-button>
          </span>
        </el-dialog>
        <!-- 图片放大 -->
        <el-dialog
         title="原图片"
         :visible.sync="dialogimageVisible"
         width="30%">
          <img :src="imgData" alt="原图片大小" style="margin-left:150px;">
           <span slot="footer" class="dialog-footer">
            <el-button @click="dialogimageVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogimageVisible = false">确 定</el-button>
          </span>
        </el-dialog>
    </div>
</template>

<script>
import {GetagentList, DelectagentList, Statisticsweekfans, Daysaddfans, Daysupdatefans, Statisticsmonthfans} from '@/api'
import VermicelliStatistics from '@/components/application/VermicelliStatistics'
import MonthVermicelli from '@/components/application/MonthVermicelli'
export default {
  name: 'ApplicationList',
  data () {
    return {
      currentPage: 1,
      totalpage: 0,
      tableData: [],
      formLabelAlign: {
        number: 0,
        id: 0
      },
      localStorageData: [],
      monthsData: [],
      DialogVisible: false,
      UpdateDialogVisible: false,
      weeksDialogVisible: false,
      monthsDialogVisible: false,
      monthvermicelliDialogVisible: false,
      dialogimageVisible: false,
      imgData: '',
      weekData: {},
      value: '',
      monthID: 0,
      pagesize: 5
    }
  },
  components: {
    VermicelliStatistics,
    MonthVermicelli
  },
  methods: {
    // 获取代理列表
    listInit (page) {
      GetagentList(page)
        .then(res => {
          if (res.errno === '0') {
            for (var i=0; i<res.agent_info.length; i++) {
              if (res.agent_info[i].city === '市辖区' || res.agent_info[i].city === '县' || res.agent_info[i].city === '市') {
                res.agent_info[i].city = ''
              }
            }
            this.tableData = res.agent_info
            this.totalpage = res.count
            this.currentPage = res.page
            this.pagesize = res.total_page
          } else if (res.errno === '4004') {
            alert('请重新登录')
            localStorage.setItem('username', '')
            localStorage.setItem('mytoken', '')
            this.$router.push({name: 'login'})
          }
        })
    },
    // 删除代理
    handleDelete (row) {
      let id = row.id
      this.$confirm('此操作将永久删除该代理, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        DelectagentList(JSON.stringify({agent_id: id}))
          .then(res => {
            if (res.errno === '0') {
              this.$message({
                type: 'success',
                message: '删除成功!'
              })
              this.listInit()
            } else {
              alert(res.errmsg)
            }
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 当前页数改变
    handleCurrentChange (val) {
      this.currentPage = val
      this.listInit(val)
    },
    // 日常加粉量
    handleAddfans (row) {
      let overTime = JSON.parse(localStorage.getItem('fans'))[0]
      let CurrentTime = new Date().getTime()
      if (CurrentTime > overTime) {
        localStorage.removeItem('fans')
        let ctimer = new Date(new Date(new Date().toLocaleDateString()).getTime()+24*60*60*1000-1).getTime()
        this.localStorageData[0] = ctimer
        localStorage.setItem('fans', JSON.stringify(this.localStorageData))
      }
      this.formLabelAlign.number = JSON.parse(localStorage.getItem('fans'))[row.id]
      if (!this.formLabelAlign.number) {
        this.DialogVisible = true
        this.formLabelAlign.id = row.id
      } else {
        this.$message({
          type: 'error',
          message: '今日粉丝量已经增加，若有错误，请修改'
        })
      }
    },
    commitFans () {
      if (this.formLabelAlign.number && this.formLabelAlign.number !== 0) {
        // 将每日增加的粉丝量添加进本地存储，方便修改
        this.localStorageData = JSON.parse(localStorage.getItem('fans'))
        this.localStorageData[this.formLabelAlign.id] = this.formLabelAlign.number
        localStorage.setItem('fans', JSON.stringify(this.localStorageData))
        let params = {today_fans: this.formLabelAlign.number, agent_id: this.formLabelAlign.id}
        // 发送添加粉丝量的请求
        Daysaddfans(JSON.stringify(params))
          .then(res => {
            if (res.errno === '0') {
              this.DialogVisible = false
              this.$message({
                type: 'success',
                message: '添加成功'
              })
            } else {
              this.DialogVisible = false
              this.$message({
                type: 'error',
                message: '添加失败'
              })
            }
          })
      } else {
        this.$message({
          type: 'error',
          message: '请输入今日增加的粉丝数'
        })
      }
    },
    // 日常加粉修改
    handleUpdatefans (row) {
      this.formLabelAlign.id = row.id
      this.formLabelAlign.number = JSON.parse(localStorage.getItem('fans'))[row.id]
      if (this.formLabelAlign.number && this.formLabelAlign.number !== 0) {
        this.UpdateDialogVisible = true
      } else {
        this.$message({
          type: 'error',
          message: '还没有添加每日增加的粉丝量'
        })
      }
    },
    updateFans () {
      // 将修改的每日增加的粉丝量添加进本地存储，方便修改
      this.localStorageData = JSON.parse(localStorage.getItem('fans'))
      this.localStorageData[this.formLabelAlign.id] = this.formLabelAlign.number
      localStorage.setItem('fans', JSON.stringify(this.localStorageData))
      let params = {today_fans: this.formLabelAlign.number, agent_id: this.formLabelAlign.id}
      Daysupdatefans(JSON.stringify(params))
        .then(res => {
          if (res.errno) {
            this.UpdateDialogVisible = false
            this.$message.success('修改成功')
          } else {
            this.UpdateDialogVisible = false
            this.$message.error('修改失败')
          }
        })
    },
    // 周加粉统计
    handleSelectweekFans (row) {
      let params = {agent_id: row.id}
      Statisticsweekfans(JSON.stringify(params))
        .then(res => {
          this.weeksDialogVisible = true
          this.weekData = res
        })
    },
    // 月加粉统计
    handleSelectmonthFans (row) {
      this.value = ''
      this.monthID = row.id
      this.monthsDialogVisible = true
    },
    monthsCommit () {
      if (this.value === '') {
        this.$message.error('请选择日期')
        return false
      }
      let params = {agent_id: this.monthID, year: this.value.substr(0, 4), month: this.value.substr(5, 2)}
      Statisticsmonthfans(JSON.stringify(params))
        .then(res => {
          this.monthsData = res
          this.monthsData['month'] = this.value.substr(5, 2)
          this.monthsDialogVisible = false
          this.monthvermicelliDialogVisible = true
        })
    },
    // 微信二维码放大
    handlecollection (img) {
      this.imgData = img
      this.dialogimageVisible = true
    },
    // 微信收款二维码
    handlewechat (img) {
      this.imgData = img
      this.dialogimageVisible = true
    }
  },
  mounted () {
    this.listInit()
  },
  created () {
    let storageData = localStorage.getItem('fans')
    if (!storageData) {
      // 获取到当天23：59分的时间
      let ctimer = new Date(new Date(new Date().toLocaleDateString()).getTime()+24*60*60*1000-1).getTime()
      this.localStorageData[0] = ctimer
      localStorage.setItem('fans', JSON.stringify(this.localStorageData))
    }
  }
}
</script>



