import axios from 'axios'
// 通过axios.defaults.baseURL设置全局的基准路径
// axios.defaults.baseURL = 'http://192.168.1.199:8888'
// 设置请求拦截，目的是拦截所有请求，如果本地有token，设置请求头，可以通过访问，如果没有，不能继续访问了
axios.interceptors.request.use(function (config) {
  // 拦截到请求后，首先判断是否有token，如果有的话，就将它取出来，放进config.headers里面
  let mytoken = localStorage.getItem('mytoken')
  if (mytoken) {
  // 设置请求头Authorization 的值为mytoken里面的值,
    config.headers['Authorization'] = mytoken
  } else {
    return config
  }
  return config
}, function (error) {
  // Do something with request error
  return Promise.reject(error)
})
// 登陆请求
export const Login = function (params = {}) {
  return axios.post('/admin/login', params, {headers: {'Content-Type': 'application/json;charset=UTF-8'}}).then(res => res.data)
}
// 获取代理列表
export const GetagentList = function (page = 1) {
  return axios.get(`/admin/agentList/${page}`).then(res => res.data)
}
// 代理审核列表
export const GetagentReview = function (page = 1) {
  return axios.get(`/admin/agentReview/${page}`).then(res => res.data)
}
// 代理申请审核
export const AgentReview = function (params = {}) {
  return axios.post('/admin/agentReview', params, {headers: {'Content-Type': 'application/json;charset=UTF-8'}}).then(res => res.data)
}
